
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'

let map = new Map()

export default {
directives: {
TransferDom
},
components: {
Popup,
Tab,
TabItem,
Swiper,
SwiperItem,
Divider,
Checker,
CheckerItem,
Group,
Cell,
XButton,
Radio,
Confirm,
CellFormPreview,
FormPreview
},
data () {
return {
labsUrl: this.$baseUrl + '/labs',
labStatusUrl: this.$baseUrl + '/lab/status',
currentUrl: this.$baseUrl + '/lab/status/current',
teachersUrl: this.$baseUrl + '/teachers',
currentStudentUrl: this.$baseUrl + '/students/current',
labStatusLogUrl: this.$baseUrl + '/labStatusLogs/current',
labs: [],
labStatus: [],
chooseLab: null,
teachers: [],
teacher: null,
index: 0,
showTeacher: false,
showConfirm: false,
listChooseLabStatus: [],
cellTitle: null,
form: {
id: null,
teacher: {
name: null
}
},
currentStudent: null,
currentLab: null,
currentLabName: null,
labStatusLog: null,
listCurrentLabStatus: [],
cancelCurrent: [{
style: 'primary',
text: '',
onButtonClick: () => {
this.$axios.get(this.currentUrl).then((res) => { // 
if (res.data) {
this.currentLab = res.data

const _this = this
this.$vux.confirm.show({
title: '',
content: '' + this.currentLabName + '(' + this.currentLab.audit + ')' + (this.currentLab.audit === '' ? ',' : '') + '',

onCancel () {
_this.getCurrent()
_this.currentLab = null
_this.currentLabName = null
},
onConfirm () {
_this.$axios.delete(_this.currentUrl).then((res) => {
_this.getCurrentStudent()
_this.getLabs()
_this.currentLab = null
_this.currentLabName = null
_this.$vux.toast.text(res.data)
}).catch((error) => {
console.debug(error.response)
})
}
})
}
})
}
}]
}
},
created () {
this.getCurrentStudent()
this.getLabs()
this.getTeachers()
},
watch: {
index: function (val) {
this.getLabStatusMap(this.labs[val].name)
}
},
methods: {
getCurrentStudent () {
this.$axios.get(this.currentStudentUrl).then((res) => {
this.currentStudent = res.data
})
},
getLabs () {
this.$axios.get(this.labsUrl).then((res) => {
this.labs = res.data

this.getCurrent()
}).catch((error) => {
console.debug(error.response)
})
},
getLabStatusMap (name) {
this.$axios.get(this.labStatusUrl, {params: {name: name, sort: 'idBookingDate,asc'}}).then((res) => {
map.set(name, res.data)
this.labStatus = res.data
}).catch((error) => {
console.debug(error.response)
})
},
getCurrent () {
this.$axios.get(this.currentUrl).then((res) => {
if (res.data) {
this.currentLab = res.data
this.currentLabName = res.data.id.lab.name
this.listCurrentLabStatus = [
{
label: '',
value: this.currentLab.id.bookingDate
},
{
label: '',
value: this.currentLab.id.bookingTimeRang
}, {
label: '',
value: this.currentLab.teacher.name
}, {
label: '',
value: this.currentLab.audit
}
]
this.labStatusLog = null

this.getLabStatusMap(this.currentLabName)
} else {

this.getLabStatusMap(this.labs[this.index].name)
this.currentLab = null
this.getCurrentLog()
}
}).catch((error) => {
console.debug(error.response)
})
},
getCurrentLog () {
this.$axios.get(this.labStatusLogUrl).then((res) => { // 
this.labStatusLog = res.data.content
})
},
getTeachers () {
this.$axios.get(this.teachersUrl).then((res) => {
this.teachers = res.data
}).catch((error) => {
console.debug(error.response)
})
},
switchTabItem (index) {
console.log('on item click:', index)
},
onCheckerItemClick (itemValue, itemDisabled) {
if (!itemDisabled) {
if (this.currentStudent.isDisable) {
this.$vux.alert.show({
title: '',
content: '' + this.currentStudent.disableDateTime + ''
})
return
}
if (this.currentLab && this.currentLab.audit === '') {
this.$vux.alert.show({
title: '',
content: '' + this.currentLabName + ''
})
return
}
if (this.currentLabName) {
const _this = this
this.$vux.confirm.show({
title: '',
content: '' + this.currentLabName + '',

onCancel () {
_this.getLabs()
},
onConfirm () {
_this.showTeacher = true
_this.chooseLab = itemValue
console.log('onCheckerItemClick:', _this.chooseLab)
}
})
} else {
this.showTeacher = true
this.chooseLab = itemValue
console.log('onCheckerItemClick:', this.chooseLab)
}
}
},
cancelTeacher () {
this.chooseLab = null
this.teacher = null
this.showTeacher = false
this.getLabs()
},
confirmTeacher () {
if (!this.teacher) {
this.$vux.toast.text('')
return
}
this.showTeacher = false
this.showConfirm = true
this.cellTitle = this.chooseLab.id.lab.name
this.listChooseLabStatus = [
{
label: '',
value: this.chooseLab.id.bookingDate
},
{
label: '',
value: this.chooseLab.id.bookingTimeRang
}, {
label: '',
value: this.teacher
}
]
},
cancelConfirm () {
this.chooseLab = null
this.teacher = null
},
confirmData () {
this.form.id = this.chooseLab.id
this.form.teacher.name = this.teacher
console.debug(this.form)
this.chooseLab = null
this.teacher = null
this.$axios.put(this.labStatusUrl, this.form).then((res) => {
this.getLabs()
this.$vux.toast.text(res.data)
}).catch((error) => {
console.debug(error.response)
})
}
}
}



